// відступи між секціями
.how-we-do {
  padding-top: 35px;
  padding-bottom: 40px;
  background-color: $whiteColor;

  @include media(tablet) {
    padding-bottom: 70px;
  }

  @include media(desktop) {
    padding-top: 65px;
    padding-bottom: 130px;
  }
}
.how-we-do__title {
  margin-bottom: 30px;

  font-weight: 800;
  font-size: 20px;
  line-height: 1.3;
  text-align: center;
  letter-spacing: 0.03em;
  color: $primaryTitleBackgroundColor;

  @include media(tablet) {
    font-size: 26px;
  }
  @include media(desktop) {
    text-align: left;
    font-size: 46px;
    margin-bottom: 47px;
  }
}
.cards {
  @include media(tablet) {
    display: flex;
    flex-wrap: wrap;
    gap: 20px;
  }
  @include media(desktop) {
    gap: 20px 30px;
  }
}
.card__item {
  padding: 30px 20px;
  background-color: $whiteColor;
  box-shadow: 0px 8px 26px rgba(0, 0, 0, 0.1);

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  @include media(mobile-only) {
    &:not(:last-child) {
      margin-bottom: 15px;
    }
  }
  @include media(tablet) {
    padding: 35px 32px;
  }
  @include media(desktop) {
    padding: 35px 28px 55px;
  }

  @include media(tablet) {
    flex-basis: calc((100% - 20px) / 2);
    display: flex;
    flex-direction: column;
    align-items: center;
  }
  @include media(desktop) {
    flex-basis: calc((100% - 60px) / 3);
  }
  &:hover,
  &:focus {
    background-color: $accentColor;
  }
  &:hover .card__icon,
  &:focus .card__icon {
    fill: $whiteColor;
  }
  &:hover .card__title,
  &:focus .card__title {
    color: $whiteColor;
  }
  &:hover .card__text,
  &:focus .card__text {
    color: $whiteColor;
  }
  &:hover .card__title::after,
  &:focus .card__title::after {
    background: $whiteColor;
  }
}
.card__icon {
  display: block;
  margin-left: auto;
  margin-right: auto;
  fill: $accentColor;
  margin-bottom: 20px;
  @include media(tablet) {
    margin-bottom: 28px;
  }

  transition: fill 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.card__title {
  position: relative;
  margin-bottom: 42px;
  @include media(desktop) {
    margin-bottom: 32px;
  }
  font-weight: 700;
  font-size: 14px;
  line-height: 1.43;

  text-align: center;
  letter-spacing: 0.08em;

  color: $primaryTitleBackgroundColor;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);
  @include media(tablet-only) {
    height: 40px;
  }
  @include media(desktop) {
    font-size: 20px;
    line-height: 1.4;
  }
}
.card__title::after {
  position: absolute;
  bottom: -20px;
  left: 50%;
  content: '';
  width: 20px;
  height: 2px;
  background-color: $accentColor;

  transition: background-color 250ms cubic-bezier(0.4, 0, 0.2, 1);
}
.card__text {
  font-weight: 500;
  font-size: 12px;
  line-height: 1.67;

  text-align: center;
  letter-spacing: 0.08em;

  color: $primaryTextColor;
  transition: color 250ms cubic-bezier(0.4, 0, 0.2, 1);

  flex-grow: 1;
  @include media(desktop) {
    font-size: 18px;
    line-height: 1.56;
  }
}
