.button {
  width: 260px;
  height: 48px;
  text-align: center;
  letter-spacing: .08em;
  text-transform: uppercase;
  cursor: pointer;
  color: #fff;
  background-color: #ff6b09;
  border: none;
  padding: 14px 0;
  font-size: 12px;
  font-weight: 700;
  line-height: 1.25;
  display: block;
}

