    font-size: 14px;
  }
}

@keyframes glowing {
  0% {
    box-shadow: 0 0 5px #ff6b09;
  }

  50% {
    box-shadow: 0 0 25px #fd7921;
  }

  100% {
    box-shadow: 0 0 5px #fa8a40;
  }
}

@keyframes glowing-icon {
  0% {
    fill: #ff6b09;
  }

  50% {
    fill: #fd7921;
  }

  100% {
    fill: #66ea3d;
  }
}

@keyframes glowing-text {
  0% {
    color: #ff6b09;
  }

  50% {
    color: #fd7921;
  }

  100% {
    color: #66ea3d;
  }
}

@keyframes scale-up-center {
  0% {
    transform: scale(.5);
  }

  100% {
    transform: scale(1);
  }
}

.hero-img {
  animation: scale-up-center 1.5s cubic-bezier(.39, .575, .565, 1) both;
}

@keyframes flip-horizontal-bottom {
  0% {
    -webkit-transform: rotateX(0);
    transform: rotateX(0);
  }

  100% {
    transform: rotateX(-180deg);
  }
}

.consult-btn:hover {
  animation: flip-horizontal-bottom .4s reverse both;
}

@keyframes flip-scale-up-hor {
  0% {
    -webkit-transform: scale(1) rotateX(0);
    transform: scale(1) rotateX(0);
  }

  50% {
    transform: scale(2.5)rotateX(-45deg);
  }

  100% {
    transform: scale(1)rotateX(-90deg);
  }
}

.nav__link:hover {
  animation: flip-scale-up-hor .5s linear;
}

.button, #toTop, .program__list:after {
  animation: glowing 1.3s infinite;
}

.accent-text {
  animation: glowing-text 1.3s infinite;
}

@keyframes wave {
  0% {
    transform: translateX(5%);
  }

  50% {
    transform: translateX(10%);
  }

  100% {
    transform: translateX(0%);
  }
}

