.problems {
  padding-top: 25px;
  padding-bottom: 35px;

  @include media(tablet) {
    padding-top: 55px;
  }

  @include media(desktop) {
    padding-top: 100px;
    padding-bottom: 65px;
  }
  background-image: linear-gradient(
    to bottom,
    $primaryTitleBackgroundColor 79%,
    $whiteColor 21%
  );

  @include media(tablet-only) {
    background-image: linear-gradient(
      to bottom,
      $primaryTitleBackgroundColor 71%,
      $whiteColor 29%
    );
  }

  @include media(desktop) {
    background-image: url(../images/campfire-bg.svg),
      linear-gradient(
        to bottom,
        $primaryTitleBackgroundColor 71%,
        $whiteColor 29%
      );
    background-repeat: no-repeat;
    background-position: top 88px right 0, center;
  }
}
.problems-title {
  margin-bottom: 25px;

  @include media(tablet) {
    margin-bottom: 30px;
    width: 450px;
  }

  @include media(desktop) {
    margin-bottom: 68px;
    width: 805px;
  }

  font-weight: 800;
  font-size: 24px;
  line-height: 1.3;
  letter-spacing: 0.03em;
  color: $whiteColor;

  @include media(desktop) {
    font-size: 46px;
  }
}

.accent-text {
  color: $accentColor;
}
.problem-list {
  padding: 20px 27px;

  @include media(tablet) {
    padding: 28px 90px;
    gap: 20px 68px;
  }

  @include media(desktop) {
    padding: 67px 113px;
    gap: 30px 40px;
  }
  //  обнулити лічильник
  counter-reset: section;

  background-color: $whiteColor;
  box-shadow: -5px 8px 26px rgba(0, 0, 0, 0.04);

  @include media(tablet) {
    display: flex;
    flex-wrap: wrap;
    justify-content: space-between;
  }
}
.problem__item {
  @include media(tablet) {
    flex-basis: calc((100% - 68px) / 2);
  }

  @include media(desktop) {
    flex-basis: calc((100% - 80px) / 3);
  }
}

.problem__item:not(:last-child) {
  @include media(mobile-only) {
    margin-bottom: 15px;
  }
}
// лічильник
.problem__text::before {
  counter-increment: section;
  content: '— 0' counter(section);
  display: block;
  font-size: 14px;
  line-height: 1.2;
  letter-spacing: 0.08em;
  color: $accentColor;
  margin-bottom: 5px;

  @include media(desktop) {
    font-size: 20px;
    line-height: 1.65;
    margin-bottom: 10px;
  }
}
.problem__text {
  font-weight: 700;
  font-size: 14px;
  line-height: 1.71;
  letter-spacing: 0.08em;

  color: $primaryTitleBackgroundColor;

  @include media(tablet) {
    line-height: 1.43;
  }

  @include media(desktop) {
    font-size: 20px;
    line-height: 1.4;
  }
}
